#cityForm {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  input {
    width: 100%;
    height: 25px;
    border: 1px solid silver;
    border-radius: 5px;
    margin-right: 5px;
  }

  div {
    width: 100%;
    margin: 10px 0;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }

  button {
    width: auto;
    min-height: 30px;
    height: auto;
    border: 1px solid silver;
    border-radius: 5px;
    margin-right: 20px;
    color: aliceblue;
    background-color: lightskyblue;
  }
}
